import React, { Component } from 'react';
import { Card, CardHeader, Col, Row, Button, Form, Input, FormGroup, Label, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import commonService from '../../../core/services/commonService';

import Loader from '../../Loader/Loader';
import CategoryData from './CategoryData';
import './Category.css'

class Category extends Component {
  constructor(props){
    super(props);
    this.state = {
      modal: false,
      category_name: "",
      categoryList: [],
      categoryImage: null,
      categoryImageUrl: "",
      loading: true,
      rowIndex: -1,
      status: "",
      errors: {category_name: '', category_image: '', error: ''},
      category_name_valid: false,
      filterItem: {},
      formValid: false,


    } 
    this.handleEditCategory = this.handleEditCategory.bind(this);
    this.submitHandler = this.submitHandler.bind(this);
    this.handleDeleteCategory = this.handleDeleteCategory.bind(this);
    this.filterItemList = this.filterItemList.bind(this);
    this.resetSearchFilter = this.resetSearchFilter.bind(this);
  }

  // Fetch the category List
  componentDidMount() { 
    this.categoryList();
  }
  /*Category List API*/
  categoryList(filterItem = {}) {
    let filterParmas = "";
    
    
    if(filterItem.custom !== undefined && filterItem.custom !== "" ) 
      filterParmas += (filterParmas !=="" ) ? "&keyword="+filterItem.custom: "?keyword="+filterItem.custom;
    if(filterItem.status !== undefined && filterItem.status !== "" ) {
      let statusText = commonService.getStatusVal(filterItem.status)
      filterParmas += (filterParmas !=="" ) ? "&status="+statusText: "?status="+statusText;
    }
    this.setState( { loading: true}, () => {
      commonService.getAPIWithAccessToken('category'+filterParmas)
        .then( res => {
          
           
          if ( undefined === res.data.data || !res.data.status ) {
            this.setState( {  loading: false } );
            toast.error(res.data.message);    
            return;
          }   

          this.setState({loading:false, categoryList: res.data.data});     
         
        } )
        .catch( err => {   
                
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            this.setState( { loading: false } );
            toast.error(err.message);    
          }
        } )
    } )
  }
  submitHandler (event) {
    event.preventDefault();
    event.target.className += " was-validated";
    if(!this.validateForm())
      return false;
    let formData = {}; 
    formData.categoryName = this.state.category_name;
    let statusText = commonService.getStatusVal(this.state.status)
    formData.status = statusText;
    formData.remark = this.state.remark;
    
    this.setState( { loading: true, formProccessing: true}, () => {
      if(this.state.rowIndex > -1){
        /* Update Category */
        const categoryId = this.state.categoryList[this.state.rowIndex].categoryId;
        formData.categoryId = categoryId;
        
        commonService.putAPIWithAccessToken( `category`, formData)
          .then( res => {
            
           
            
            if ( undefined === res.data.data || !res.data.status ) { 
              let errors = {};
              if(res.data.errors !== undefined)
                errors = res.data.errors;
              else
                errors.categoryName = res.data.message;
              this.setState( { formProccessing: false, loading: false, errors: errors} );
              return false;
            }  
            let updatedCategoryLists = this.state.categoryList;
            updatedCategoryLists[this.state.rowIndex] = res.data.data;
            this.setState({modal: false, rowIndex: "", formProccessing: false, loading:false});
            toast.success(res.data.message);    
            this.categoryList();
          } )
          .catch( err => {       
              
            if(err.response !== undefined && err.response.status === 401) {
              localStorage.clear();
              this.props.history.push('/login');
            }
            else {
              this.setState( { formProccessing: false, loading:false } );
              toast.error(err.message);    
            }
          } )
       
      }
      else{
         /* Add Category */
        commonService.postAPIWithAccessToken( `category`, formData)
          .then( res => {
            
           
            
            if ( undefined === res.data.data || !res.data.status ) { 
              let errors = {};
              if(res.data.errors !== undefined)
                errors = res.data.errors;
              else
                errors.categoryName = res.data.message;
              this.setState( { formProccessing: false, loading:false, errors: errors} );
              return false;
            }            
            this.setState({modal: false, formProccessing: false, loading:false});
            toast.success(res.data.message); 
            this.categoryList();
          } )
          .catch( err => {
            if(err.response !== undefined && err.response.status === 401) {
              localStorage.clear();
              this.props.history.push('/login');
            }
            else{
              this.setState( { formProccessing: false, loading:false } );
              toast.error(err.message); 
            }
          } )
      }
    })
  };
  /* Input Field On changes*/
  changeHandler = event => {
    const name = event.target.name;
    const value = event.target.value
    this.setState({ [name]: value });
  };
  
 
  /* Validate Form */
  validateForm() {
    let formValid = true;
    let formField = this.state; 
    let errors = {};
    if(formField.category_name === undefined || formField.category_name.trim() === "" ){
      formValid = false;
      errors['categoryName'] = 'Studies name field required';
    }
    if(formField.status === undefined || formField.status.trim() === "" ){
      formValid = false;
      errors['status'] = 'Status field required';
    }
    this.setState({errors:  errors});
    return formValid;
    
  }
  /* Set Error Class*/
  errorClass(error) {
    return(error.length === 0 ? '' : 'has-error');
  }

  toggle = () => {
    this.setState({
      modal: !this.state.modal,
      category_name: "",
      remark: "",
      status: "",
      rowIndex: -1,
      formValid: false,
      errors: {},
      categoryImageUrl: ""
    });
  }
  /* File changes handler*/
  changeFileHandler = event => {   
    const targetFile = event.target.files[0];
    let formErrors = this.state.formErrors;
    formErrors.category_image = "";
    
    if(targetFile.type !== "image/png" && targetFile.type !== "image/jpeg" && targetFile.type !== "image/jpg" && targetFile.type !== "image/svg") {
      formErrors.category_image = " accept only png, jpeg, jpg";
      this.setState({ categoryImage: targetFile, formErrors: formErrors  });
    }
    else
      this.setState({ categoryImage: targetFile, formErrors: formErrors  });
  };

  /* Edit Category*/
  handleEditCategory(rowIndex){
   
      const categoryItem = this.state.categoryList[rowIndex];
      let status = "Active"
      status = commonService.getStatus(categoryItem.status);

      this.setState({modal: true, status: status, category_name: categoryItem.categoryName, remark: categoryItem.remark, rowIndex: rowIndex, formValid: true, category_name_valid: true, categoryImageUrl: categoryItem.imagUrl});
  }
  /* Add category */
  handleDeleteCategory(rowIndex){
   
    const categoryItem = this.state.categoryList[rowIndex];
   
    this.setState( { loading: true}, () => {
      commonService.deleteAPIWithAccessToken( `category/`+categoryItem.categoryId)
        .then( res => {
          this.setState({loading: false});
          if ( undefined === res.data || !res.data.status ) {            
             toast.error(res.data.message);      
            return;
          }         
          
          toast.success(res.data.message);
          this.categoryList();
        } )
        .catch( err => {       
            
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else{
            this.setState( { loading: false } );
            toast.error(err.message);
          }
      } )
    })
  }

  filterItemList(){
    const filterItem = this.state.filterItem;
    this.categoryList(filterItem);
  }
  
  changeFilterHandler = event => {
    const name = event.target.name;
    const value = event.target.value;
    const filterItem = this.state.filterItem
    filterItem[name] = value;
    this.setState({ filterItem: filterItem });
  };
  resetSearchFilter() {
    this.setState({filterItem: { status: '', custom: ''}});
    this.categoryList();
  }

  render() {

    const { categoryList, loading, modal, errors  } = this.state;     
    let loaderElement ='';
    
    if(loading)
      loaderElement = <Loader />
    /*let categoryImagePreview = '';
    if(this.state.categoryImageUrl !== "")
      categoryImagePreview = <div className="imagePreview"><img src={this.state.categoryImageUrl} alt="" /></div>*/

    return (
      <div className="animated fadeIn">
        <Row>
          
          {loaderElement}
          <Col lg={12}>
            <Card className="oep-card ">
                <CardHeader className="mainHeading">
                  <strong>Studies List</strong> 
                  <Button color="" className="categoryAdd" type="button" onClick={this.toggle}><i className="fa fa-plus"></i> Add Studies</Button>
                </CardHeader>
                <Row>
                  <Col md={12}>
                    <div className="search-filter">
                      <Row>                      
                        
                        <Col md={"3"}>
                          <FormGroup> 
                            <Label htmlFor="filter_doctor_id">Search By Name</Label>            
                            <Input type="text" placeholder="Search By Name" id="custom" name="custom" value={this.state.filterItem.custom} onChange={this.changeFilterHandler} />
                          </FormGroup>  
                        </Col>
                        <Col md={"3"}>
                          <FormGroup> 
                            <Label htmlFor="filter_doctor_id">Search By Status</Label>            
                            <Input type="select" placeholder="Search By Status" id="status" name="status" value={this.state.filterItem.status} onChange={this.changeFilterHandler}>
                                <option value = "">Select</option>
                                <option value="Active">Active</option>
                                <option value="Inactive">Inactive</option>
                                <option value="Pending">Pending</option>
                                <option value="Under Negotiation">Under Negotiation</option>
                            </Input>
                          </FormGroup>  
                        </Col>
                        <Col md={"3"}>
                          <FormGroup className="filter-button-section"> 
                            <Button className="search-btn" type="button" onClick={this.filterItemList}>Search</Button> 
                            <Button className="search-btn" id="resetButton" type="button" onClick={this.resetSearchFilter}>Reset</Button> 
                          </FormGroup>             
                        </Col>
                      </Row>
                    </div>  
                  </Col>       
                  <Col lg= {12}>   
                    <CategoryData data={categoryList} toggle={this.toggle} editCategoryAction={this.handleEditCategory} deleteCategoryAction={this.handleDeleteCategory} dataTableLoadingStatus = {this.state.loading} />
                  </Col> 
                </Row>
            </Card>
          </Col>
        </Row>
        <Modal isOpen={modal} toggle={this.toggle} className="category-modal-section">
          <ModalHeader toggle={this.toggle}>Studies</ModalHeader>
          <Form onSubmit={this.submitHandler} noValidate>
            <ModalBody>
              <FormGroup> 
                <Label htmlFor="category_name">Studies Name</Label>            
                <Input type="text" placeholder="Studies Name *" invalid={errors['categoryName'] !== undefined && errors['categoryName'] !== ""} id="category_name" name="category_name" value={this.state.category_name} onChange={this.changeHandler} required />
                <div className="invalid-feedback">
                  {errors['categoryName']}
                </div>
              </FormGroup>
              <FormGroup> 
                <Label htmlFor="category_name">Remark</Label>            
                <Input type="textarea" placeholder="Remark " id="remark" name="remark" value={this.state.remark} onChange={this.changeHandler} />
              </FormGroup>
              <FormGroup>
                <Label htmlFor="template_status">Status</Label>            
                <Input type="select" placeholder="Status *" invalid={errors['status'] !== undefined && errors['status'] !== ""} id="status" name="status" value={this.state.status} onChange={this.changeHandler} required >
                  <option value="">Select Status</option>
                  <option value="Active">Active</option>
                  <option value="Inactive">Inactive</option>
                  <option value="Pending">Pending</option>
                  <option value="Under Negotiation">Under Negotiation</option>
                </Input>
                <div className="invalid-feedback">
                  {errors['status']}
                </div>
              </FormGroup>
               
            </ModalBody>
            <ModalFooter>
              <Button color="primary" disabled={this.state.formProccessing} type="submit">Submit</Button>
              <Button color="secondary" onClick={this.toggle}>Cancel</Button>
            </ModalFooter>
          </Form>
        </Modal>
      </div>

    )
  }
}

export default Category;
